 <template> 
    <div id="PokemonView" >
        <div class="pokemo">
            <h1>Su Pokemon es</h1>
            <h2>Nombre: {{name}}</h2> 
            <img :src="image" alt="">
            <img :src="image2" alt="">
            <p>Habilidad: {{ habilidad1 }} - Experiencia: {{ experiencia }}</p>
            <p>Habilidad 1: {{ habilidad2 }} </p>
            <p>Id pokemon : {{ id }}</p>
            <RouterLink to="/">Volver al Home</RouterLink>
        </div>
    </div>

</template> 
<script>
import axios from 'axios';

 
export default { 
    name:'PokemonView',
    props:['id'],
    data: function(){
        return{
            name:'',
            image:'',
            image2:'',
            habilidad1:'',
            habilidad2:'',
            experiencia:'',

        }
    }, 
    methods:{
        fetchPokemon(){ 
            axios.get(`https://pokeapi.co/api/v2/pokemon/${this.id}`) 
                .then(resp=>{ 
                    console.log(resp); this.setData(resp.data); 
                }) 
                .catch(error=>{ console.log(error) 
                }) 
            }, 
            setData(data){ 
                this.name = data.name; 
                this.image = data.sprites.front_default; 
                this.image2 = data.sprites.back_shiny; 
                this.habilidad1= data.abilities[0].ability.name;
                this.habilidad2= data.abilities[1].ability.name;
                this.experiencia= data.base_experience;

            } 
        }, 
        //hook: se pasa cuando la funcion que consume la api esta funciona
        created(){ 
            this.fetchPokemon(); 
        }
} 
</script> 
<style scoped> 
#PokemonView{
    text-align: center;
}
.pokemo{
    margin-left: 10%;
    text-align: center;
    background-color: crimson;
    border: solid 5px black;
    width: 80%;
    color: bisque;
}
img{
    width: 50%;
    height: 50%;
}

</style>