<template>
    <div id="Foto">
        <h1>Componente Foto independiente</h1>
        <h1>Foto {{id}}</h1> 
        <img :src="src" alt=""> <br>
        <router-link to="/fotos">Volver</router-link> 
    

    </div>
</template>
<script>
export default{
    name:'Foto',
    data: function(){
        return{
            src:'',
        }
    },
    computed: { id(){ 
        return this.$route.params.id 
    } 
    }, 
    methods: { // -- Metodos 
    }, 
    // components: {}, 
    created(){ 
        this.src = ` https://picsum.photos/200/300?grayscale/${this.id}` 
    } 
}

</script>
<style scoped>
#Foto{
    background-color: rgb(205, 57, 242);
}


</style>