<template>
    <div id="FotosView">
        <h1>Componente Fotos</h1>
        <div class="grid_foto"> 
            <div v-for="i in 12" :key="i"> 
                <h3>Foto {{i}}</h3> 
                <a @click="redirect(i)"> 
                    <img v-bind:src="srcImage(i)" alt=""> 
                </a> 
            </div> 
        </div>

    </div>
</template>
<script>
export default{
    name:'FotosView',
    data: function(){

    },
    methods:{
        srcImage(id){ 
            return ` https://picsum.photos/200/300?grayscale/${id}` 
        }, 
        redirect(id){ 
            this.$router.push(`/fotos/${id}`); 
        }

    }
}

</script>
<style scoped>
#FotosView{
    background-color: lightgreen;
}
.grid_foto{ 
    display:grid; 
    grid-template-columns: 1fr 1fr 1fr; 
    }
</style>