<template>
  <div class="HomeComponent">
    <form> 
      <h1>Buscador</h1> 
      <label for="txtPokemon" >Ingrese Id Pokemon (1-200)</label> <br>
      <input type="text" id="txtPokemon" v-model="pokemon_id"> <br><br>
      <button @click.prevent="buscador">Buscar</button> {{pokemon_id}}
    </form>
  </div>
</template>

<script>
// @ is an alias to /src

export default { 
  name:"HomeComponent", 
  data:function(){ 
    return { 
      pokemon_id:'', 
      } 
    }, 
    methods:{ 
      buscador: function(){ 
        //evaluamos si la variable esta vacía finaliza el trabajo del metodo buscar
        if(this.pokemon_id === "") 
        return 
        //si no esta vacío se muestra esto
        this.$router.push(`/pokemon/${this.pokemon_id}`) 
      } 
    } 
  } 
  </script> 

  <style scoped> 
  form{ 
    border:2px solid #e117d1; 
    background-color: lightcoral;
    padding-bottom: 20px; 
    width:60%; 
    margin:0 auto; 
    } 
    </style>
